import React from 'react';
import Standalone from '../components/layouts/standalone'
import { graphql } from 'gatsby'
import BlogSummary from '../components/blog-summary'
import SEO from '../components/seo'
import moment from 'moment'
import LazyLoader from '../components/lazy-loader'

const Blog = ({ data }) => {
  const now = moment();
  return (
  <Standalone>
    <SEO title={'Expatty | Blog'} description={ data.site.siteMetadata.description } keywords={ data.site.siteMetadata.commonKeywords } />
    <LazyLoader buffer={ 500 }/>
    <main>
      <div className="container-fluid blog">
        <h1 className="title--header">Patty's Blog</h1>
        <div className="row">
          <div className="col-md">
            {
              data.bloglist.edges.filter(({ node }) => now.isAfter(node.frontmatter.date)).map(({ node }, idx) => (
                <BlogSummary
                  key={ idx }
                  excerpt={ node.excerpt }
                  image={ node.frontmatter.image }
                  url={ node.fields.slug }
                  title={ node.frontmatter.title }
                  author={ node.frontmatter.author }
                  date={ node.frontmatter.date }/>
              ))
            }
          </div>
        </div>
      </div>
    </main>
  </Standalone>
)};

export default Blog;

export const pageQuery = graphql`
    query SiteDataForBlog {
        site {
            siteMetadata {
                title
                slogan
                description
                commonKeywords
            }
        }
        bloglist: allMarkdownRemark(sort: {fields: frontmatter___date, order: DESC},
            filter: {frontmatter: {type: {eq: "blog"}}}) {
            edges {
                node {
                    fields {
                        slug
                    }
                    excerpt
                    frontmatter {
                        title
                        image
                        date
                        author
                    }
                }
            }
        }
    }
`